function formatDateString(date: string): string {
  const parts = date.split('-');
  return new Date(parts[0] + '/' + parts[1] + '/' + parts[2]).toLocaleDateString('ru');
}

function changeDate(date: string, multiplier: number): string {
  const parts = date.split('-');
  const result = new Date(parts[0] + '/' + parts[1] + '/' + parts[2]);

  result.setDate(result.getDate() - multiplier * 7);

  return result.toLocaleDateString('ru');
}

function formatDateText(text: string, value: number): string {
  let result = '';

  if (text === '') {
    switch (defineWordEndingByNumber(value)) {
      case 1:
        result = ' ';
        break;
      case 2:
        result = ' ';
        break;
      case 3:
        result = ' ';
        break;
    }
  }

  if (text === '') {
    switch (defineWordEndingByNumber(value)) {
      case 1:
        result = ' ';
        break;
      case 2:
        result = ' ';
        break;
      case 3:
        result = ' ';
        break;
    }
  }

  if (text === '') {
    switch (defineWordEndingByNumber(value)) {
      case 1:
        result = ' ';
        break;
      case 2:
        result = ' ';
        break;
      case 3:
        result = ' ';
        break;
    }
  }

  if (text === '') {
    switch (defineWordEndingByNumber(value)) {
      case 1:
        result = ' ';
        break;
      case 2:
        result = ' ';
        break;
      case 3:
        result = ' ';
        break;
    }
  }

  if (text === '') {
    switch (defineWordEndingByNumber(value)) {
      case 1:
        result = ' ';
        break;
      case 2:
        result = ' ';
        break;
      case 3:
        result = ' ';
        break;
    }
  }

  if (text === '') {
    switch (defineWordEndingByNumber(value)) {
      case 1:
        result = ' ';
        break;
      case 2:
        result = ' ';
        break;
      case 3:
        result = ' ';
        break;
    }
  }

  return result;
}

function defineWordEndingByNumber(number: number): number {
  let n = number % 100;

  if (n >= 5 && n <= 20) {
    return 2;
  }

  n = number % 10;

  if (n > 1 && n < 5) {
    return 3;
  }

  if (n === 1) {
    return 1;
  }

  return 2;
}

export { changeDate, formatDateString, formatDateText };